body {
  font-size: 16px;
  font-family: "Alegreya", serif;
  background-color: #232323;
}

h1, h2, h3 {
  text-align: center;
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4.2rem;
  font-weight: 400;
  letter-spacing: 1.8px;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2.9rem;
}

p, li {
  font-size: 2.1rem;
  letter-spacing: 0.4px;
