.container {
  margin: 0 auto;
  padding: 2rem 0;
  max-width: 1200px;
  width: 97%;
}

.centered {
  margin: 0 auto;
}

nav {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 481px) {
  nav {
    width: 50%;
    flex-direction: row;
  }
}
nav a {
  text-align: center;
  font-size: 2.2rem;
}
nav a:not(:last-child) {
  margin-bottom: 1rem;
}
@media (min-width: 481px) {
  nav a:not(:last-child) {
    margin-bottom: 0;
  }
}
nav a:hover, nav a#active-section {
  font-weight: bold;
  color: #ff6060;
}
nav a:before {
  display: block;
  content: attr(data-hover);
  font-weight: 700;
  height: 0;
  visibility: hidden;
}

#noscript-alert {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: rgba(48, 45, 2, 0.795);
  padding: 0.3rem 1rem;
  text-align: center;
  z-index: 99;
}
#noscript-alert p {
  margin: 0;
  font-size: 2.2rem;
  color: #e9e90f;
}
@media (min-width: 1025px) {
  #noscript-alert {
    padding: 0.5rem;
  }
}

.font-number {
  font-family: serif;
  letter-spacing: 1.4px;
}

.hidden {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

#index-body {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  background-color: #181818;
  animation: galleryBgColor 0.8s;
  animation-timing-function: cubic-bezier(0.55, 0.06, 0.68, 0.19);
}
@keyframes galleryBgColor {
  from {
    background-color: #ca2121;
  }
  to {
    background-color: #181818;
  }
}
#index-body #index-overlay {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.33);
  position: absolute;
  top: 0;
  left: 0;
}
#index-body main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
#index-body main #index-tittle {
  width: 100%;
  color: white;
  padding: 2rem;
  background-color: rgba(39, 4, 9, 0.9);
  backdrop-filter: blur(4px);
  box-shadow: 0 6px 12px 4px rgba(0, 0, 0, 0.35);
}
#index-body main #index-tittle h1 {
  font-size: 5rem;
  margin-bottom: 1rem;
}
@media (min-width: 481px) {
  #index-body main #index-tittle h1 {
    margin-bottom: 2rem;
    font-size: 5rem;
  }
}
@media (max-width: 319px) {
  #index-body main #index-tittle h1 {
    font-size: 220%;
  }
}
@media (min-width: 540px) {
  #index-body main #index-tittle nav {
    width: 48rem;
  }
}
#index-body main #index-tittle nav a {
  font-size: 2.7rem;
  letter-spacing: 2px;
}
#index-body #index-gallery, #index-body #index-gallery-noscript {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 481px) {
  #index-body #index-gallery, #index-body #index-gallery-noscript {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1025px) {
  #index-body #index-gallery, #index-body #index-gallery-noscript {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media (min-width: 1500px) {
  #index-body #index-gallery, #index-body #index-gallery-noscript {
    grid-template-columns: repeat(7, 1fr);
  }
}
#index-body #index-gallery img, #index-body #index-gallery-noscript img {
  animation: imagePadding 0.8s;
  animation-timing-function: linear;
}
@keyframes imagePadding {
  from {
    padding: 1rem;
  }
  to {
    padding: 0rem;
  }
}

header {
  background-color: rgba(39, 4, 9, 0.9);
  backdrop-filter: blur(6px);
  box-shadow: 0 3px 6px 2px rgba(0, 0, 0, 0.35);
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
}
header #header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  padding: 1rem 2rem;
}
@media (min-width: 411px) {
  header #header-content {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
header #header-content h1 {
  margin: 0;
}
@media (max-width: 319px) {
  header #header-content h1 {
    font-size: 3.4rem;
  }
}
header #header-content svg {
  width: 4rem;
  margin: 1rem 0;
  cursor: pointer;
  stroke: #ffffff;
  user-select: none;
}
header #header-content svg:hover {
  stroke: #ff6060;
}
@media (max-width: 319px) {
  header #header-content svg {
    margin: 0.7rem 0;
  }
}
@media (min-width: 411px) {
  header #header-content svg {
    margin: 0;
  }
}
@media (min-width: 813px) {
  header #header-content svg {
    display: none;
  }
}
header #header-content nav {
  width: 48rem;
  padding: 0.8rem 0;
}
@media (min-width: 481px) {
  header #header-content nav {
    margin: 0 auto;
  }
}
@media (min-width: 813px) {
  header #header-content nav {
    margin: 0;
    padding-left: 6rem;
    display: inherit;
  }
}

header, #headerFill {
  min-height: 12.85rem;
}
@media (min-width: 411px) {
  header, #headerFill {
    min-height: 7rem;
  }
}

#headerFill {
  background-color: grey;
}

/* FOOTER */
footer {
  background-color: rgba(39, 4, 9, 0.9);
  color: white;
  padding-top: 1.2rem;
}
footer #footer-content p {
  margin: 0;
}
footer #footer-content > div:not(:last-of-type) {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(248, 248, 248, 0.2);
}
footer #footer-content > div:nth-of-type(1) {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1025px) {
  footer #footer-content > div:nth-of-type(1) {
    flex-direction: row;
  }
}
footer #footer-content > div:nth-of-type(1) div {
  display: flex;
  margin-bottom: 1rem;
  width: 26.4rem;
}
@media (min-width: 1025px) {
  footer #footer-content > div:nth-of-type(1) div {
    margin-bottom: 0;
  }
}
footer #footer-content > div:nth-of-type(1) div p {
  margin-left: 1.5rem;
}
footer #footer-content > div:nth-of-type(2) {
  text-align: center;
}
footer #footer-content > div:nth-of-type(2) p {
  margin-bottom: 1.5rem;
}
@media (min-width: 481px) {
  footer #footer-content > div:nth-of-type(2) p {
    margin-bottom: 0.8rem;
  }
}
footer #footer-content > div:nth-of-type(3) {
  text-align: center;
}
footer #footer-content > div:nth-of-type(3) p {
  font-size: 1.8rem;
}
footer #footer-content > div:nth-of-type(3) a {
  text-decoration: underline;
}
@media (min-width: 1025px) {
  footer #footer-content > div:nth-of-type(3) {
    text-align: right;
  }
}

#body-fotografia #content, #body-fotografia #noscript-content {
  max-width: 2000px;
  margin: 0 auto;
  padding: 0.3rem;
  padding-bottom: 4rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 481px) {
  #body-fotografia #content, #body-fotografia #noscript-content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1025px) {
  #body-fotografia #content, #body-fotografia #noscript-content {
    grid-template-columns: repeat(4, 1fr);
  }
}
#body-fotografia #content img:not(.fullsize-image img), #body-fotografia #noscript-content img:not(.fullsize-image img) {
  padding: 0.3rem;
  user-select: none;
  cursor: pointer;
}
#body-fotografia #content img:not(.fullsize-image img):hover, #body-fotografia #noscript-content img:not(.fullsize-image img):hover {
  outline: 4px solid rgba(182, 51, 51, 0.432);
  outline-offset: -3px;
  transition: outline 0.15s ease;
}
#body-fotografia #content .fullsize-image, #body-fotografia #noscript-content .fullsize-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
}
#body-fotografia #content .fullsize-image img, #body-fotografia #noscript-content .fullsize-image img {
  width: auto;
  height: auto;
  max-height: 95vh;
  max-width: 95vw;
  border: 4px solid #c7c7c7;
  user-select: none;
}